
body{
  background: rgb(34,193,195);
  background: linear-gradient(86deg, rgba(34,193,195,1) 0%, rgba(253,187,45,1) 100%); 
  font-family: 'Work Sans', sans-serif;
  color: whitesmoke;
  font-size:1.25em;
  font-weight:300;
}

button{
  cursor:pointer;
  border:none;
  color:rgb(34,193,195);
  background-color:whitesmoke;
  padding:1%;
  margin:2%;
  font-family: 'Work Sans', sans-serif;
  transition:linear 0.5s;
  font-size:1em;
}

button:hover{
  background: rgb(34,193,195);
  color:whitesmoke;
}
h1{
  font-family: 'Lobster', cursive;
  text-align:center;
  font-size: 3em;
  color: whitesmoke;
  letter-spacing:0.05em;
}
h2{
  font-family: 'Lobster', cursive;
}
.new-game-container, .victory-container{
  text-align:center;
}
.scene {
  position:relative;
  width: 30%;
  height: 200px;
  border: 1px solid #CCC;
  margin: 1%;
  perspective: 600px;
}
@media screen and (min-width: 600px){
  .scene{
    width:13%;
    height:200px
  }
}

.click-container{
  text-align:center;
}
.tiles-container{
  display:flex;
  flex-direction:row;
  flex-wrap: wrap;
  justify-content:space-evenly;
  padding: 5%;
}
.card {
  width: 100%;
  height: 100%;
  transition: transform 1s;
  transform-style: preserve-3d;
  cursor: pointer;
  position: relative;
}

.card.is-flipped {
  transform: rotateY(180deg);
}

.card__face {
  position: absolute;
  width: 100%;
  height: 100%;
  line-height: 260px;
  color: white;
  text-align: center;
  font-weight: bold;
  font-size: 40px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.card__face--front {
  background: rgb(34,193,195);
}

.card__face--back {
  background: aquamarine;
  transform: rotateY(180deg);
  display:flex;
  align-items: center;
  justify-content: center;
}